<template>
	<section class="intro" id="intro">
		<div class="ui-carousel">
			<!-- <swiper :modules="[EffectFade]" autoplay="autoplay" effect="fade" :slides-per-view="1" :space-between="50" navigation :pagination="{ clickable: true }" :scrollbar="{ draggable: true }" @swiper="onSwiper" @slideChange="onSlideChange"> -->
			<swiper
				:modules="modules"
				:autoplay="swiperOptions.autoplay"
				:speed="swiperOptions.speed"
				:effect="swiperOptions.effect"
				:loop="swiperOptions.loop"
				Parallax
				@swiper="onSwiper" @slideChange="onSlideChange">
				<swiper-slide v-for="(slideImage, index) in slideImages" :key="index">
					<img :src="slideImage.src" alt="">
				</swiper-slide>
			</swiper>
		</div>
		<div class="filter"></div>
		<div class="title">
			<h4>Reach the peak of</h4>
			<h3><em>UI·UX</em><br> Constant<br> Attempt</h3>
			<h5>절정의 UI·UX에 이르기 위한<br> 멈추지 않는 시도</h5>
		</div>
	</section>
</template>

<script>
// import Swiper core and required modules
import { Autoplay, EffectFade, Navigation, Pagination, Parallax, Scrollbar, A11y } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/css';
import 'swiper/css/effect-fade';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';

export default {
	name: 'DefaultIntro',
	components: {
		Swiper,
		SwiperSlide
	},
	data: function() {
		let slideImages = [];
		for (let i = 1; i < 24; i++) {
			let n = `${i < 10? `0${i}` : i}`
			slideImages.push(
				{ src: require(`@/assets/img/intro/blue/${n}.jpg`) }
			)
		}
		return {
			slideImages,
			swiperOptions: {
				loop: false,
				autoplay: {
					delay: 80
				},
				speed: 80,
				effect: "fade",
				pagination: {
					el: '.swiper-pagination'
				},
				navigation: {
					nextEl: '.swiper-button-next',
					prevEl: '.swiper-button-prev',
				}
			}
		}
	},	
	setup() {
		const onSwiper = (swiper) => {
			console.log(swiper);
		};
		const onSlideChange = () => {
			console.log('slide change');
		};
		return {
			onSwiper,
			onSlideChange,
			modules: [Autoplay, EffectFade, Navigation, Pagination, Parallax, Scrollbar, A11y]
		};
	}
}
</script>

