<template>
    <div id="map"></div>
</template>

<script>

export default {
    name: "KakaoMap",
    data() {
        return {
            imgSrc : require('@/assets/img/piino_location2.png')
        }
    },
    mounted() {
        if (window.kakao && window.kakao.maps) {
            this.initMap();
        } else {
            const script = document.createElement("script");
            /* global kakao */
            script.onload = () => kakao.maps.load(this.initMap);
            //location.origin으로 주소창 문자열 구분 
            script.src = (location.origin.indexOf("localhost") > -1) ? "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=3de1bd8670d929f5310fdad4ec5412c7" : "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=224b0d220097a5b391f2149731e88af1";
            //배포
            //script.src = "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=224b0d220097a5b391f2149731e88af1";
            //로컬
            // script.src = "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=3de1bd8670d929f5310fdad4ec5412c7";
            document.head.appendChild(script);
        }
    },
    methods: {
        initMap() {
            const container = document.getElementById("map");
            const options = {
                center: new kakao.maps.LatLng(37.552,126.9161128),
                level: 4,
            };

            //지도 객체를 등록합니다.
            //지도 객체는 반응형 관리 대상이 아니므로 initMap에서 선언합니다.
            this.map = new kakao.maps.Map(container, options);

            //일반 지도와 스카이뷰로 지도 타입을 전환할 수 있는 지도타입 컨트롤을 생성합니다
            var mapTypeControl = new kakao.maps.MapTypeControl(); 
            // 지도에 컨트롤을 추가해야 지도위에 표시됩니다
            // kakao.maps.ControlPosition은 컨트롤이 표시될 위치를 정의하는데 TOPRIGHT는 오른쪽 위를 의미합니다
            this.map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);

            // 지도 확대 축소를 제어할 수 있는  줌 컨트롤을 생성합니다
            var zoomControl = new kakao.maps.ZoomControl();
            this.map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);

            var imageSrc = this.imgSrc, // 마커이미지의 주소입니다    
            imageSize = new kakao.maps.Size(48, 52), // 마커이미지의 크기입니다
            imageOption = {offset: new kakao.maps.Point(27, 69)}; // 마커이미지의 옵션입니다. 마커의 좌표와 일치시킬 이미지 안에서의 좌표를 설정합니다.

            // 마커의 이미지정보를 가지고 있는 마커이미지를 생성합니다
            var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imageOption),
                markerPosition = new kakao.maps.LatLng(37.552,126.9161128); // 마커가 표시될 위치입니다

            // 마커를 생성합니다
            var marker = new kakao.maps.Marker({
            position: markerPosition,
            image: markerImage // 마커이미지 설정 
            });

            // 마커가 지도 위에 표시되도록 설정합니다
            marker.setMap(this.map);  

            // 커스텀 오버레이에 표출될 내용으로 HTML 문자열이나 document element가 가능합니다
            var content = '<div class="customoverlay">' +
                '  <a href="https://map.kakao.com/?itemId=1317763714" target="_blank">' +
                '    <span class="title">PIINOUXD</span>' +
                '  </a>' +
                '</div>';

            // 커스텀 오버레이가 표시될 위치입니다 
            var position = new kakao.maps.LatLng(37.552,126.9161128);  

            // 커스텀 오버레이를 생성합니다
            var customOverlay = new kakao.maps.CustomOverlay({
                map: this.map,
                position: position,
                content: content,
                yAnchor: 1 
            });
        },
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#map {
    width: 640px;
    height: 560px;
}
</style>

<style>
#map .customoverlay {position:relative;right: 7px;bottom:85px;border-radius:6px;border: 1px solid #ccc;border-bottom:2px solid #ddd;float:left;}
#map .customoverlay:nth-of-type(n) {border:0; box-shadow:0px 1px 2px #888;}
#map .customoverlay a {display:block;text-decoration:none;color:#000;text-align:center;border-radius:6px;font-size:14px;font-weight:bold;overflow:hidden; background: #003046 url(https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/arrow_white.png) no-repeat right 14px center;}
#map .customoverlay .title {display:block;text-align:center;background:#fff;margin-right:35px;padding:10px 15px;font-size:14px;font-weight:bold;}
#map .customoverlay:after {content:'';position:absolute;margin-left:-12px;left:50%;bottom:-12px;width:22px;height:12px;background:url('https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/vertex_white.png')}

</style>
